@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background: #242831;
  font-family: 'Oswald',sans-serif;
  font-size: 1rem;
}
*{text-align:center}* a{text-decoration:none}.banner{width:100%;height:100vh;display:flex;position:relative;justify-content:center;align-items:center;background:url(/portfolio/static/media/bg3.bc9b73a1.png) no-repeat 50%/cover}.banner h1,.banner p{color:#fff}.banner .banner-content{display:flex;justify-content:end;align-items:center;flex-direction:column}.banner .social-links{display:flex;flex-wrap:wrap;width:100%;align-items:center;justify-content:center}.banner .social-links a{align-items:center;justify-content:center;width:40px;height:40px;border-radius:50%;margin:auto;display:flex;color:#fff}.banner .social-links .github{background-color:#fff;color:#24292e}.banner .social-links .github:hover{background-color:#24292e;color:#fff;transition:all .5s}.banner .social-links .linkedin{background-color:#0077b5}.banner .social-links .linkedin:hover{color:#0077b5;background:#fff;transition:all .5s}.banner .social-links .instagram{background:linear-gradient(45deg, #f09433, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888)}.banner .social-links .instagram:hover{background:linear-gradient(45deg, #fff, #fff);color:#f09433;transition:all .5s}.banner .social-links .facebook{background:#4267b2}.banner .social-links .facebook:hover{background:#fff;color:#4267b2;transition:all .5s}.banner .social-links .twitter{background:#1da1f2}.banner .social-links .twitter:hover{color:#1da1f2;background:#fff;transition:all .5s}.banner .social-links .codepen{background:#fff;color:#000}.banner .social-links .codepen:hover{color:#fff;background:#000;transition:all .5s}.banner .social-links .hackerrank{background:#1ba94c}.banner .social-links .hackerrank:hover{color:#1ba94c;background:#fff;transition:all .5s}.banner .banner-buttons{display:flex;align-items:center;justify-content:space-between;margin:30px 0 0}.banner .banner-buttons .project-button,.banner .banner-buttons .hire-button{margin:20px 0;border-radius:5px;font-size:15px;padding:10px 20px;letter-spacing:.5px;width:175px}.banner .banner-buttons .project-button{background:linear-gradient(45deg, #f8501c, #ffab1d);color:#fff}.banner .banner-buttons .hire-button{background:linear-gradient(45deg, #0004fc, #c300ff);color:#fff}.banner .banner-buttons .project-button:hover{background:linear-gradient(45deg, #c13dff, #f573c3);color:#000;transition:all .5s}.banner .banner-buttons .hire-button:hover{background:linear-gradient(45deg, #ee1e5d, #e644f5);color:#000;transition:all .5s}

nav{
    background-color: #000;
}
nav .navbar-brand{
    display: flex;
}

nav .navbar-brand h1{
    font-size: 25px;
    color: white;
    margin-left: 7px;
    margin-bottom: 0;
}

nav .navbar-brand .logo{
    width: 30px;
    height: 30px;
}

i{
    color: white;
}

nav .nav-item{
    margin: 5px 20px 5px auto!important;
}

nav .nav-link{
    color: #d9d9d9;
    font-size: 15px;
    font-weight: 500;
    border-bottom: 3px solid transparent;
}

nav .project{
    color: wheat;
    background: linear-gradient(90deg,#0ef,#ff00ea,#fffb00);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

nav .project:hover{
    background: linear-gradient(90deg,#fffb00,#ff00ea,#0ef);
    color: wheat;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

nav .resume{
    background: linear-gradient(45deg,#0004fc,#c300ff);
    color: #ffffff;
    border-radius: 5px;
    border-bottom: none;
}

nav a.resume:hover{
    border-bottom: none;
    background: linear-gradient(45deg,#f8501c,#ffab1d);
    color: #000;
    transition: all .5s;
}

nav .nav-link:hover{
    color: #ffffff;
    border-bottom: 3px solid #8601fe;
}

.navbar-left-auto{
    text-align: end;
    margin-left: auto;
}
section.project{margin:30px 0}section.project .project-h1{color:#fff}.comming-soon{background-image:url("https://png.pngtree.com/thumb_back/fh260/background/20200714/pngtree-modern-double-color-futuristic-neon-background-image_351866.jpg");height:60vh;background-position:center;background-size:cover;position:relative;color:white;font-family:"Courier New", Courier, monospace;font-size:25px}.comming-soon .middle{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);text-align:center}.comming-soon h1{font-weight:600}.comming-soon hr{margin:auto;width:40%;height:3px}.slider-inner{display:flex;align-items:center;width:100%;height:60vh;border-radius:10px;background-color:#000;color:#fff}.slider-inner .slider-texts{width:457.82px;height:30vh;display:flex;flex-direction:column;justify-content:center;align-items:center}.slider-inner .slider-texts h2.slider-title{font-size:2.2rem;background:linear-gradient(90deg, #0ef, #ff00ea, #fffb00);-webkit-background-clip:text;background-clip:text;-webkit-text-fill-color:transparent}.slider-inner .slider-texts p{font-size:15px;text-align:justify;margin:15px 0;font-family:"Roboto",sans-serif;line-height:26px}.slider-inner .slider-texts .slider-languages{display:flex;align-items:center;justify-content:space-evenly;width:100%;flex-wrap:wrap;padding:10px 0 20px}.slider-inner .slider-texts #svg-object{width:24px;padding:0}.slider-inner .project-links{display:flex;align-items:center;justify-content:space-evenly;width:100%;padding:20px 0 0}.slider-inner .project-links a.project-link{width:40px;height:40px;background:#fff;color:#000;border-radius:50%;display:flex;align-items:center;justify-content:center;cursor:pointer}.slider-inner .project-links a.project-link:hover{box-shadow:0 0 4px 2px #0ed1df}.slider-inner img.project-image{width:65%;height:100%;object-fit:fill;border-top-right-radius:10px;border-bottom-right-radius:10px}@media (max-width: 789px){.comming-soon{height:400px}.slider-inner{flex-direction:column;height:auto}.slider-texts{display:contents !important}img{padding-top:10px;width:100% !important}}

